.about {
  text-align: center;
  @include rmin(320) {
    padding: 50px 0; }
  @include rmin(768) {
    padding: 94px 0; } }

.about__title {
  color: #ff8400;
  text-transform: uppercase;
  font-weight: 400; }

.about__subtitle {
  font-size: 36px;
  font-weight: 600;
  margin-top: 14px; }

.about__list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap; }

.item {
  @include rmin(320) {
  	width: 100%; }
  @include rmin(425) {
  	width: 40%; }
  @include rmin(768) {
  	width: 31%; }
  display: flex;
  flex-direction: column;
  justify-content: center; }

.item_margin-top {
  @include rmin(320) {
    margin-top: 50px; }
  @include rmin(768) {
    margin-top: 93px; } }

.image-bg {
  background-image: url("../img/item-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  padding: 30px; }

.item__title {
  font-weight: 500;
  @include rmin(320) {
    margin: 10px 0; }
  @include rmin(768) {
    margin: 24px 0; } }
