html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

* {
  font-family: "Roboto", sans-serif;
}

@media only screen and (min-width: 320px) {
  * {
    font-size: 13px;
  }
}

@media only screen and (min-width: 425px) {
  * {
    font-size: 15px;
  }
}

@media only screen and (min-width: 768px) {
  * {
    font-size: 18px;
  }
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.container {
  margin: 0 auto;
  max-width: 1205px;
  padding: 0 20px;
}

.container_flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.header {
  position: relative;
  background-image: url("../img/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
}

.header a {
  color: #ffffff;
  font-weight: 400;
}

@media only screen and (min-width: 320px) {
  .header {
    padding: 25px 0 50px 0;
  }
}

@media only screen and (min-width: 425px) {
  .header {
    padding: 25px 0 100px 0;
  }
}

.top-line {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (min-width: 320px) {
  .nav:hover > .nav__list {
    display: block;
  }
}

@media only screen and (min-width: 768px) {
  .nav:hover > .nav__list {
    display: flex;
  }
}

.nav-mobile {
  width: 50px;
}

.nav-mobile:hover {
  cursor: pointer;
}

@media only screen and (min-width: 320px) {
  .nav-mobile {
    display: block;
  }
}

@media only screen and (min-width: 768px) {
  .nav-mobile {
    display: none;
  }
}

.nav-line {
  display: block;
  height: 6px;
  width: 50px;
  background-color: #ff8400;
  border-radius: 5px;
}

.nav-line:nth-child(2) {
  margin: 5px 0;
}

.nav__list {
  margin-top: 21px;
}

@media only screen and (min-width: 320px) {
  .nav__list {
    padding: 20px 0;
    display: none;
    position: absolute;
    width: 200px;
    border-radius: 80px;
    top: 44px;
    right: 0;
    text-align: center;
    background-image: url("../img/header-bg.png");
  }
}

@media only screen and (min-width: 768px) {
  .nav__list {
    padding: 0;
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: auto;
    border-radius: 0;
    top: 44px;
    right: 0;
    text-align: right;
    background-image: none;
  }
}

@media only screen and (min-width: 320px) {
  .list__item {
    margin-left: 0;
    padding: 10px;
  }
}

@media only screen and (min-width: 768px) {
  .list__item {
    margin-left: 40px;
    padding: 0;
  }
}

@media only screen and (min-width: 892px) {
  .list__item {
    margin-left: 63px;
  }
}

@media only screen and (min-width: 320px) {
  .list__link {
    padding: 10px;
    font-size: 18px;
  }
}

@media only screen and (min-width: 768px) {
  .list__link {
    padding: 0;
    font-size: 18px;
  }
}

.list__link:hover {
  color: #ff8400;
  transition: 0.5s;
}

.content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (min-width: 320px) {
  .content {
    margin-top: 20px;
  }
}

@media only screen and (min-width: 768px) {
  .content {
    margin-top: 123px;
  }
}

@media only screen and (min-width: 320px) {
  .article {
    max-width: 100%;
  }
}

@media only screen and (min-width: 768px) {
  .article {
    max-width: 50%;
  }
}

.article__title {
  font-size: 38px;
  font-weight: 500;
  margin-top: 60px;
}

.article__desc {
  margin-top: 40px;
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
}

.more-link {
  display: inline-block;
  text-transform: uppercase;
  padding: 20px 63px;
  border: 2px solid #ffffff;
  border-radius: 5px;
  transition: 0.5s;
}

.more-link:hover {
  background-color: white;
  color: #ff8400;
}

@media only screen and (min-width: 320px) {
  .more-link {
    margin: 30px 0;
  }
}

@media only screen and (min-width: 768px) {
  .more-link {
    margin: 90px 0;
  }
}

@media only screen and (min-width: 320px) {
  .main-image {
    display: none;
  }
}

@media only screen and (min-width: 768px) {
  .main-image {
    display: block;
  }
}

.about {
  text-align: center;
}

@media only screen and (min-width: 320px) {
  .about {
    padding: 50px 0;
  }
}

@media only screen and (min-width: 768px) {
  .about {
    padding: 94px 0;
  }
}

.about__title {
  color: #ff8400;
  text-transform: uppercase;
  font-weight: 400;
}

.about__subtitle {
  font-size: 36px;
  font-weight: 600;
  margin-top: 14px;
}

.about__list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media only screen and (min-width: 320px) {
  .item {
    width: 100%;
  }
}

@media only screen and (min-width: 425px) {
  .item {
    width: 40%;
  }
}

@media only screen and (min-width: 768px) {
  .item {
    width: 31%;
  }
}

@media only screen and (min-width: 320px) {
  .item_margin-top {
    margin-top: 50px;
  }
}

@media only screen and (min-width: 768px) {
  .item_margin-top {
    margin-top: 93px;
  }
}

.image-bg {
  background-image: url("../img/item-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  padding: 30px;
}

.item__title {
  font-weight: 500;
}

@media only screen and (min-width: 320px) {
  .item__title {
    margin: 10px 0;
  }
}

@media only screen and (min-width: 768px) {
  .item__title {
    margin: 24px 0;
  }
}

.join {
  background-image: -webkit-linear-gradient(141deg, #ff8400 0%, #fb3d3c 100%);
  color: #ffffff;
  text-align: center;
}

@media only screen and (min-width: 320px) {
  .join {
    padding: 50px 0;
  }
}

@media only screen and (min-width: 768px) {
  .join {
    padding: 150px 0;
  }
}

.join__article {
  margin: 0 auto;
}

@media only screen and (min-width: 320px) {
  .join__article {
    width: 100%;
  }
}

@media only screen and (min-width: 425px) {
  .join__article {
    width: 65%;
  }
}

.join__title {
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 6px;
}

@media only screen and (min-width: 320px) {
  .join__title {
    font-size: 20px;
  }
}

@media only screen and (min-width: 768px) {
  .join__title {
    font-size: 30px;
  }
}

.join__desc {
  margin-top: 7px;
}

@media only screen and (min-width: 320px) {
  .join__desc {
    line-height: 20px;
  }
}

@media only screen and (min-width: 768px) {
  .join__desc {
    line-height: 36px;
  }
}

.email-input {
  background: none;
  border: 2px solid #ffffff;
  border-radius: 5px;
  height: 61px;
  padding: 0 30px;
  color: #ffffff;
  outline: none;
}

.email-input::-webkit-input-placeholder {
  color: #ffffff;
}

.email-input:-moz-placeholder {
  color: #ffffff;
}

.email-input:-ms-input-placeholder {
  color: #ffffff;
}

@media only screen and (min-width: 320px) {
  .email-input {
    width: 80%;
    margin-top: 50px;
  }
}

@media only screen and (min-width: 768px) {
  .email-input {
    width: 535px;
    margin: 90px 30px 0 0;
  }
}

.submit-btn {
  height: 63px;
  width: 200px;
  background: #ffffff;
  color: #ff8400;
  border: none;
  border-radius: 5px;
  transition: 0.5s;
  box-shadow: 0px 0px 25px 0px rgba(255, 132, 0, 0.65);
}

.submit-btn:hover {
  cursor: pointer;
  background: #fc5a59;
  color: #ffffff;
}

@media only screen and (min-width: 320px) {
  .submit-btn {
    margin-top: 50px;
  }
}

@media only screen and (min-width: 768px) {
  .submit-btn {
    margin-top: 90px;
  }
}

.contacts {
  background-color: #f1f1f1;
  border-bottom: 3px solid #d8d8d8;
  padding: 10px 0;
}

.contact {
  margin: 15px 0;
}

.contact__link {
  color: #5c5c5c;
  position: relative;
  padding-left: 60px;
}

.contact__link::before {
  display: block;
  position: absolute;
  top: -7px;
  left: 0;
}

.contact__link_phone::before {
  content: url("../img/ic-phone.png");
}

.contact__link_email::before {
  content: url("../img/ic-mail.png");
}

.contact__link_address::before {
  content: url("../img/ic-location.png");
}

.footer {
  padding: 15px;
}

@media only screen and (min-width: 320px) {
  .container_footer {
    justify-content: center;
  }
}

@media only screen and (min-width: 768px) {
  .container_footer {
    justify-content: space-between;
  }
}

.copy {
  margin: 10px 0;
  color: #5c5c5c;
  text-align: center;
}

.social-networks {
  margin: 10px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media only screen and (min-width: 320px) {
  .social {
    margin-left: 20px;
  }
}

@media only screen and (min-width: 768px) {
  .social {
    margin-left: 40px;
  }
}

.social__link {
  fill: #868686;
}

.social__link:hover {
  fill: #ff8400;
  transition: 0.3s;
}
