.footer {
  padding: 15px; }

.container_footer {
  @include rmin(320) {
    justify-content: center; }
  @include rmin(768) {
    justify-content: space-between; } }

.copy {
  margin: 10px 0;
  color: #5c5c5c;
  text-align: center; }

.social-networks {
  margin: 10px 0;
  display: flex;
  flex-direction: row;
  align-items: center; }

.social {
  @include rmin(320) {
    margin-left: 20px; }
  @include rmin(768) {
    margin-left: 40px; } }

.social__link {
  fill: #868686;
  &:hover {
    fill: $hover_color;
    transition: 0.3s; } }
